import React, { useState } from 'react';
import { useEffect } from 'react';
import SunEditor from 'suneditor-react';
import 'suneditor/dist/css/suneditor.min.css';
import {
    align,
    font,
    fontColor,
    fontSize,
    formatBlock,
    hiliteColor,
    horizontalRule,
    lineHeight,
    list,
    paragraphStyle,
    table,
    template,
    textStyle,
    image,
    link,
    fullScreen
} from 'suneditor/src/plugins';

const TextEditor = ({ id, value, setValue }) => {

    return (
        <SunEditor
            id={id}
            autoFocus={true}
            lang="en"
            setOptions={{
                showPathLabel: false,
                minHeight: '50vh',
                maxHeight: '50vh',
                placeholder: 'Enter your text here ...',
                plugins: [
                    align,
                    font,
                    fontColor,
                    fontSize,
                    formatBlock,
                    hiliteColor,
                    horizontalRule,
                    lineHeight,
                    list,
                    paragraphStyle,
                    table,
                    template,
                    textStyle,
                    image,
                    link
                ],
                buttonList: [
                    ['undo', 'redo'],
                    ['font', 'fontSize', 'formatBlock'],
                    ['paragraphStyle'],
                    ['bold', 'underline', 'italic', 'strike', 'subscript', 'superscript'],
                    ['fontColor', 'hiliteColor'],
                    ['removeFormat'],
                    '/', // Line break
                    ['outdent', 'indent'],
                    ['align', 'horizontalRule', 'list', 'lineHeight'],
                    ['table', 'link', 'image'],
                    ['fullScreen']
                ],
                formats: ['p', 'div', 'h1', 'h2', 'h3', 'h4', 'h5', 'h6'],
                font: [
                    'Arial',
                    'Calibri',
                    'Comic Sans',
                    'Courier',
                    'Garamond',
                    'Georgia',
                    'Impact',
                    'Lucida Console',
                    'Palatino Linotype',
                    'Segoe UI',
                    'Tahoma',
                    'Times New Roman',
                    'Trebuchet MS'
                ]
            }}
            setDefaultStyle="font-family: Arial; font-size: 16px;"
            setContents={localStorage.getItem(id) || value[`${id}Ans`]}
            value={value[`${id}Ans`]}
            onChange={(value) => {
                setValue(value);
                localStorage.setItem(id, value);
            }}
        />
    );
};

export default TextEditor;
